@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap);
@import url(https://kit.fontawesome.com/a1fdc9fb5b.js);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	font-family: 'Source Sans Pro';
	font-weight: 400;
	font-size: 20px;
	line-height: 150%;
	background-color: rbga(255,255,255,0);
	color: #454545;
}

.section-content {
	margin-left: 15%;
	margin-right: 15%;
	/*padding-bottom: 48px;*/
} 	

.section-header {
	display: flex;
  	justify-content: center; 
  	align-items: center;
	height: 120px;
	text-transform: uppercase;
}
.section-spacer {
	height: 100px;
}

.section-header p {
	text-transform: uppercase;
	font-weight: 400;
	font-family: 'Open Sans';
	letter-spacing: 2px;
	font-size: 18px;
	border-bottom: 1px solid #000;
    padding-bottom: 0.5px;
}

.page-content {
	/*margin-bottom: 64px;*/
	background: white;
}
.header-bar {
  display:flex;
  justify-content: space-around;
  background-color: rgba(255,255,255,1);
  align-items: center;
  padding: 0 10%;
  position:fixed;
  width: 100%;
  height: 100px;
  top: 0;
  color: rbg(69,69,69);
  z-index: 1000;
  box-shadow: 0 2px 5px 2px rgba(245,245,245,0.7);
}

.header-bar div {
  flex-grow: 1;
}

/********************************/
/***  Title (left hand side)  ***/
/********************************/
.title {
  text-align: left;
}

#name-logo {
  height: 60px;
  cursor: pointer;
}

/************************************/
/***  Nav links menu (right side) ***/
/************************************/
.nav-links {
	display: flex;
  justify-content: flex-end;
}
.nav-links li {
  list-style: none;
  padding: 2px 16px;
}

.nav-links a {
  text-decoration: none;
  color: inherit;
	letter-spacing: 3px;
  font-size: 16px;
  cursor: pointer;
  font-family: 'Source Sans Pro';
  font-weight: 400;
  text-transform: uppercase;
}

.nav-links a:hover {
  font-weight: 600;
}

/*********************/
/***  BURGER MENU  ***/
/*********************/

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #e2e2db;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #373a47 ;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the 
animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  top: 0px;
}

/* General sidebar styles */
.bm-menu {
  background: #f9f9f9;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #4b5556;
  text-transform: uppercase;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: white !important;
  opacity: 0 !important;
}
/*links in the burger menu*/
.bm-item-list a {
  padding: 12px;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

  
.home-spacer {
    height: 100px;
}
.home-body {
    display: flex;
    /*justify-content: space-between;*/
    justify-content: center;
    height: 80vh;/*750px;*/
    align-content: stretch;
    color: white;
}
.left-side {
    width: 50%;
    /*background: #f0e4d8;*/
    /*background: #e2e2db;*/
    /*background: #a8d0e6;*/
    display:flex;
    align-items: center;
    justify-content: center;
}
.left-side img {
    max-width: 60%;
    height:auto;
}

.right-side {
    width:50%;
    display: flex;
    flex-direction: column;
    justify-content: center; 
    flex-flow: column wrap;
}
.welcome-text {
    position: relative;
    padding: 0 64px 0 32px;
}

.welcome-text #hello {
    font-size: 95px;
    font-family: 'Source Sans Pro';
    font-weight: 600;
    color: #264849 !important;
}
.welcome-text #intro {
    font-size:1.75em;
    font-weight: 500;
    color: #264849;
}

.intro-container {
    display: flex;
}
.intro-container p {
    margin-right: 0.333em;
}
.intro-container #rotating-text {
    font-size: 1.75em;
    font-weight: 700;
    height: 75px;    
    color: #264849 !important;
}

.external-links{
    padding: 24px 32px;
}

.external-links a {
    padding-top: 64px;
    padding-right: 40px;
}

.external-links svg path {
    color: #264849;
}


.project-row {
	display:flex;
	justify-content: center;
	text-align: center;
}

.project-item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 500px;
	height: 500px;
	margin: 1.5em;
	background-color: rgba(38, 72, 73, 0.2);
	box-shadow: 1px 1px 10px rgba(245,245,245,0.7);
}

@media screen and (max-width: 1000px) {
	.project-row {
		flex-direction: column;
		flex-flow: column wrap;
	}
}

.project-media img:hover {
	transform: scale(1.01);
	opacity: 0.8;
}


/*Project Description*/
.project-text{
	min-height: 6em;
	margin-top: 2em;
	padding: 0.7em;
	background-color:white;
}
.project-text h3 {
	font-family: 'Source Sans Pro';
	font-size: 0.8em;
	font-weight: bold;
	text-transform: uppercase;
}
.project-text p {
	text-align: left;
	font-size: 0.75em;
}



.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 180px;
    background-color: white;
    padding: 0 80px;
}

.contact-links a {
    text-decoration: none;
    color: inherit;
    padding: 24px;
}

.about-container {
    display: flex;
    justify-content: center;
}
.about-description {
    max-width: 700px;
}
.about-description p {
    font-size: 19px;
    font-weight: 400;
    text-align: justify;
}

#about-me-section-header p {
    border-bottom: 1px solid #2e3340;
}

.experience-list {
    list-style: none;
}
.experience-list li {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 36vh;
    margin: 1em;
    min-height: 300px;

}

.experience-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 16px;
}

.experience-image {
    width: 40%;
    margin-right: 8px;
}
.experience-image img {
    /*max-width: 30vh;
    max-height: 30vh;*/
    width: 70%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}

.experience-image img:hover{
    opacity: 0.8;
    transform: scale(1.01);
}
.experience-text {
    width: 60%;
    display: flex;
    flex-direction: column;
}
.experience-title{
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: max(18px, 2.2vh);
}
.experience-company {
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: max(18px, 1.5vh);
}
.experience-description {
    height: 16vh;
    margin-top: 2vh;
    font-size: 0.95em;
    overflow: scroll;
}


